import React from 'react'
import {Link} from 'react-router-dom'
import HorrorThumb from '../../images/filmesAmaldiçoados/filmesAmaldiçoadosThumbnail.jpg'
const Card = () =>{
    return(
        <div className="article-thumbnail">
        <img src={HorrorThumb} className="thumbnail-image" alt="Coraline Thumbnail"/>
        
        <div className="article-thumbnail-texts">
            <h4>Filmes de Terror Amaldiçoados na Vida Real</h4>
            
            <p>Confira nessa matéria filmes amaldiçoados contendo acontecimentos e histórias macabras em seus bastidores. Esses fatos vão desde presença de demônios até mortes...</p> 
        </div>
        <div className="view-button-area">
            <Link to="/filmes-amaldiçoados-na-vida-real" className="view-button">Visualizar</Link> 
            </div>  
    </div>
    )
}

export default Card