import React from 'react'
import {Switch, Route, Redirect} from 'react-router-dom'

import Welcome from '../welcome/Welcome'
import Riddle1 from '../riddles/Riddle1'
import Riddle2 from '../riddles/Riddle2'
import Riddle3 from '../riddles/Riddle3'
import Riddle4 from '../riddles/Riddle4'

import Main from '../main/Main'
import Curiosidades from '../sites/Curiosidades'
import CuriosidadesSobreCoraline from '../sites/CuriosidadesCoraline'
import FilmesAmaldiçoados from '../sites/10FilmesAmaldiçoados'
import AVerdadeiraAnnabelle from '../sites/AVerdadeiraAnnabelle'
import JogosBizarros from '../sites/JogosBizarros'
function Routes(){
    return(
        <Switch>
            <Route exact path="/welcome" component={Welcome}/>
            <Route  path="/riddle1" component={Riddle1}/>
            <Route  path="/riddle2" component={Riddle2}/>
            <Route  path="/riddle3" component={Riddle3}/>
            <Route  path="/riddle4" component={Riddle4}/>
            <Route exact path="/mundo-nerd-home" component={Main}/>
            <Route  path="/mundo-nerd-curiosidades" component={Curiosidades}/>

            <Route exact path="/curiosidades-sobre-coraline" component={CuriosidadesSobreCoraline}/>

            <Route path="/filmes-amaldiçoados-na-vida-real" component={FilmesAmaldiçoados}/>

            <Route path="/a-verdadeira-annabelle" component={AVerdadeiraAnnabelle}/>

            <Route path="/jogos-bizarros" component={JogosBizarros}/>

            <Redirect from="*" to="/welcome"/>
        </Switch>
    )
}

export default Routes