import React,{useEffect} from 'react'
import './css/Content.css'

const HomeContent = () =>{
    useEffect(()=>{
        window.scrollTo(0,0)
    },[])//função para sempre renderizar o topo da página.
    return(
                 <section className="content">
                 <h2>Bem Vindo!</h2>
                <p><i>Hello There,</i> bem vindo ao Mundo Nerd um site feito por entusiasta do mundo geek, aqui você vai encontrar matérias relacionadas a filmes, séries, HQs dentre outras coisas. O conteúdo é tirado de fontes confiáveis com link para as mesmas, aproveite!</p>

                <h2> Fique de Olho <i className="fas fa-eye"></i></h2>

                
                <p>Ao longo do site foram deixados vários easter eggs relacionados a séries, filmes, jogos dentre outros, preste a atenção e ache todas as referências, garanto que você vai reconhecê-las.</p>
             </section>
    )
}

export default HomeContent