import React from 'react'
import {Link} from 'react-router-dom'
import Hatoful from '../../images/jogosBizarros/hatoful-boyfriend.jpg'

const JogosBizzarosCard = () =>{
    return(
        <div className="article-thumbnail">
        <img src={Hatoful} className="thumbnail-image" alt="Coraline Thumbnail"/>
        
        <div className="article-thumbnail-texts">
            <h4>Jogos com propostas bizarras para você se divertir.</h4>
            
            <p>Veja sobre alguns jogos com uma temática e propostas um tanto quanto inusitadas para você se divertir, simuladores de animais, comidas e profissões são alguns exemplos.</p> 
        </div>
        <div className="view-button-area">
            <Link to="/jogos-bizarros" className="view-button">Visualizar</Link> 
            </div>  
    </div>
    )
}

export default JogosBizzarosCard