import React from 'react'

import Goat from '../images/jogosBizarros/goat.jpeg'
import jfk from '../images/jogosBizarros/jfk.jpg'
import Hatoful from '../images/jogosBizarros/hatoful-boyfriend.jpg'
import IamBread from '../images/jogosBizarros/iambread.jpg'
import MrMosquito from '../images/jogosBizarros/mrmosquito.jpg'
import Seaman from '../images/jogosBizarros/seaman.jpg'
import Surgeon from '../images/jogosBizarros/surgeon-simulator.jpg'
import WhosYourDaddy from '../images/jogosBizarros/whos-your-daddy.jpg'








export default function JogosBizarros() {
    return (
        <section className="content">
            <h2>Jogos com as propostas mais bizarras que você precisa conhecer</h2>

            <p>Goat Simulator, I am Bread e Surgeon Simulator são exemplos de jogos de sucesso que ganharam o público com suas temáticas incomuns. Os games colocam o player na pele de uma cabra, de um pão, e no papel de um médico durante uma cirurgia, respectivamente. Apesar de inusitadas, as propostas agradaram o público. Veja, a seguir, dez games que impressionam por ter jogabilidades ou missões bizarras.</p>

            <h3>Goat Simulator</h3>

            <figure>
                <img src={Goat} alt="goat-simulator" className="image"/>
                <figcaption>
                Apesar da proposta diferente, Goat Simulator é um dos games estranhos que mais fizeram sucesso — Foto: Divulgação/Microsoft
                </figcaption>
            </figure>

            <p>Goat Simulator foi lançado em 2014 e atualmente está disponível para PC, Xbox One e celulares Android e iPhone (iOS). Ele é mais um daqueles jogos que acabou caindo nas graças dos criadores de conteúdo e por isso ganhou relevância no cenário. A produção simula a vida de uma cabra, sendo que é possível chifrar e lamber os mais diversos objetos do ambiente. Além da proposta bem humorada, o jogo é bastante simples, já que o único objetivo é sair tocando o terror em uma cidade. A cada missão cumprida, o player ganha uma quantidade de pontos que vão se somando ao longo do game.</p>

            <h3>I am Bread</h3>

            <figure>
                <img src={IamBread} alt="i am bread" className="image"/>
                <figcaption>
                    I am Bread é considerado um game do gênero de simulação — Foto: Divulgação/Bossa Studio
                </figcaption>
            </figure>

            <p>O jogo I am Bread foi lançado em 2015 pela desenvolvedora Bossa Studios e está disponível para PC, PlayStation 4, Xbox One e celulares Android e iPhone (iOS). A produção indie possui a bizarra proposta de colocar o jogador no controle de uma fatia de pão de forma. Apesar de os comandos serem complicados, o objetivo do game é simples. Para vencer em uma fase basta sair do ponto A e chegar até o B, que no caso é uma torradeira que assará o alimento. Apesar de bastante diferente, o jogo fez sucesso e tem milhares de horas de gameplay no YouTube.</p>

            <h3>Who’s Your Daddy</h3>

            <figure>
                <img src={WhosYourDaddy} alt="Who’s Your Daddy" className="image"/>
                <figcaption>
                Who’s Your Daddy funciona como uma disputa entre pai e filho — Foto: Divulgação/Steam
                </figcaption>
            </figure>

            <p>Who’s Your Daddy é um jogo que pode ser baixado para PC, através da Steam. Ele foi desenvolvido e lançado em 2015 por Joe Williams, um programador norte-americano. O game é um multiplayer competitivo e tem a proposta de promover um embate caseiro entre um pai desatento e um bebê travesso.</p>

            <p>Quem escolhe o adulto precisa evitar que a criança acabe morrendo em um acidente doméstico, seja por causa do forno, objetos cortantes, afogamento na piscina, peças metálicas na tomada e mais. O jogador na pele do bebê vence a partida se contrariar os cuidados do pai e arranjar alguma forma de se acidentar. Apesar de ser polêmico, o título tem milhares de fãs.</p>

            <h3>Seaman</h3>

            <figure>
                <img src={Seaman} alt="Seaman" className="image"/>

                <figcaption>
                    A imagem do peixe com cara humana de Seaman causa até um desconforto — Foto: Divulgação/Vivarium
                </figcaption>
            </figure>

            <p>Seaman foi um jogo lançado pela primeira vez no Japão, em 1999, para o console Dreamcast, videogame desenvolvido pela Sega. Esse é um daqueles títulos que está presente em praticamente qualquer lista de jogos bizarros. O game singleplayer tem a proposta de fazer com que o jogador cuide de um peixe como se fosse um Tamagotchi, aqueles bichichos virtuais que fizeram sucesso nos anos 90. Até aí, nada parece incomum.</p>

            <p>Tudo começa a ficar estranho quando se percebe que o peixe tem um rosto e comportamentos humanos. Para deixar tudo mais confuso ainda, de acordo com a evolução do animal, ele vai criando patas e se transformando em uma espécie de sapo. O título é um marco na indústria de como não se deve desenvolver pets virtuais.</p>
            
            <h3>Mister Mosquito</h3>

            <figure>
                <img src={MrMosquito} alt="Mister Mosquito" className="image"/>
                <figcaption>
                    Os gráficos de Mister Mosquito são bem característicos da era PlayStation 2 — Foto: Reprodução/YouTube Vysethedetermined2
                </figcaption>
            </figure>

            <p>O jogo eletrônico Mister Mosquito foi lançado em 2001 para PlayStation 2. Ele é mais um título que chegou primeiro ao Japão e nos anos seguintes desembarcaram no resto do mundo. Justamente como o nome sugere, o game tem a missão estranha de colocar o jogador para controlar um mosquito. O pequeno inseto vive especificamente na casa da família Yamada, sendo que o objetivo do animal é sugar o sangue dos humanos.</p>

            <p>As missões vão sendo cumpridas a partir do momento que o jogador consegue sugar e armazenar sangue o suficiente para que o mosquito sobreviva durante o inverno. Para concluir a missão, é possível atrapalhar os humanos criando distrações, como voar em direção aos interruptores para apagar a luz. No meio da batalha para conseguir alimento, é preciso ficar atento ao nível de estresse dos algozes, que com apenas uma pancada podem matar o inseto.</p>

            <h3>JFK Reloaded</h3>

            <p>O game JFK Reloaded foi lançado em 2004 para PC. Mais que bizarro, a temática do título é de extremo mau gosto. As mecânicas do game são simples, a ideia é refazer o assassinato do ex-presidente norte-americano John F. Kennedy, que morreu após ser baleado pelo ex-fuzileiro Lee Harvey Oswald em novembro de 1963. O título tem uma pontuação que varia de acordo com a física do tiro disparado pelo jogador, que precisa acertar o ex-presidente. O jogo foi chamado de "desprezível" pelo irmão de John Kennedy e chegou a sofrer um boicote da imprensa que cobre a indústria de videogames.</p>

            <figure>
                <img src={jfk} alt="JFK Reloaded" className="image"/>
                <figcaption>
                 JFK Reloaded é bizarro e de mau gosto ao mesmo tempo — Foto: Reprodução/YouTube indylover2010
                </figcaption>
            </figure>

            <h3>Hatoful Boyfriend</h3>

            <p>Lançado em 2011, Hatoful Boyfriend é um jogo que está disponível para PC, Mac, Linux, PlayStation 4 e celulares Android e Iphone (iOS). O game foi desenvolvido por criadores de mangá e é dos gêneros novela visual e simulador de encontros. A proposta é bastante bizarra, já que o objetivo do jogador é encontrar um pássaro para formar um par perfeito. A história se passa em uma universidade frequentada por aves, incluindo pombos e outras espécies, chamada St. PigeoNation’s Institute. O protagonista é o único humano do jogo que ingressa nessa universidade para cumprir o objetivo de encontrar seu amor verdadeiro.</p>

            <p>Apesar de ser uma das criações mais estranhas do universo dos games, Hatoful Boyfriend recebeu algumas críticas positivas. Como a jogabilidade é basicamente escolher linhas de diálogos, os textos do jogo são bastante extensos e bem escritos. E como são diversas as opções, o game tem vários finais diferentes e aposta no lado cômico da situação para cativar os jogadores.</p>

            <figure>
                <img src={Hatoful} alt="Hatoful Boyfriend" className="image"/>
                <figcaption>
                    Hatoful Boyfriend é um game com estética de mangá — Foto: Divulgação/Steam
                </figcaption>
            </figure>

            <h3>Surgeon Simulator</h3>
            <figure>
                <img src={Surgeon} alt="Surgeon Simulator" className="image"/>
                <figcaption>
                    Surgeon Simulator possui várias das ferramentas médicas, como bisturis, serras, facas, seringas e mais — Foto: Divulgação/Bossa Studios
                </figcaption>
            </figure>

            <p>Surgeon Simulator é um jogo lançado em 2013 e que atualmente está disponível para PC, Mac, Linux, PlayStation 4 e smartphones Android e Iphone (iOS). O simulador, que foi desenvolvido pela Bossa Studios, mesma empresa responsável por I am Bread, é voltado para pessoas que possuem estômago forte. A proposta do game é colocar o jogador na pele de um cirurgião que precisa realizar operações delicadas para salvar os pacientes. O jogo termina se o paciente morrer por causa de uma hemorragia.</p>

            <p>No meio de todo esse processo, é preciso serrar ossos, cortar tecidos internos, pegar órgãos com a mão, aplicar injeções e mais. Apesar do apelo cômico, o game não é recomendado para pessoas impressionáveis, mesmo os gráficos do jogo não sendo realistas. Surgeon Simulator tem notas medianas porque além do apelo estranho, tem controles ruins e pode ficar repetitivo.</p>

            <p>
                    <a href="https://www.techtudo.com.br/listas/2020/02/dez-jogos-com-as-propostas-mais-bizarras-que-voce-precisa-conhecer.ghtml" target="blank" rel="noreferrer">Fonte das informações</a>
                    
                </p>
        </section>
    )
}
