import React from 'react'
import {Link} from 'react-router-dom'
import Annabelle from '../../images/annabelle/annabelle.jpg'


const AnnabelleCard = () =>{
    return(
        <div className="article-thumbnail">
        <img src={Annabelle} className="thumbnail-image" alt="Coraline Thumbnail"/>
        
        <div className="article-thumbnail-texts">
            <h4>Conheça a verdadeira Annabelle</h4>
            
            <p>Descubra sobre a boneca que inspirou a boneca Annabelle nos cinemas, elas são bem diferentes, mas seus poderes sobrenaturais são iguais. A Annabelle original pode parecer inofensiva, mas ela <b>não é</b>.</p> 
        </div>
        <div className="view-button-area">
            <Link to="/a-verdadeira-annabelle" className="view-button">Visualizar</Link> 
            </div>  
    </div>
    )
}

export default AnnabelleCard