import React, {Component} from 'react'
import './css/Riddles.css'
import {Link} from 'react-router-dom'

class Riddle1 extends Component{
    render(){
        return(
            <div className="container-fluid d-flex flex-column justify-content-center align-content-center" id="div">
              
                <p>00110111 00101011 00110001 00100000 00111101 00100000 00111111 ?</p>
                
                <div className="container-fluid d-flex flex-column justify-content-center align-items-center">

                
                    <input type="number" className="form-control" id="input-number" placeholder="Resultado..."/>
               
                    <button type="button" className="btn btn-secondary" id="button" onClick={onClick}>Enviar</button>
                </div>

                <div className="error-message-area">
                <span className="text-danger value-null d-none message">INSIRA UM VALOR VÁLIDO.</span>

                <span className=" error-message text-alert d-none message">TENTE NOVAMENTE.</span>
                </div>

                <div className="button-area">
                    <button type="button" className="btn btn-success mt-2 p-3 d-none" id="btn-nl">
                        <Link to="/riddle2" className="link">Continuar</Link>
                    </button>
                </div>
            </div>
        )
    }
}

function onClick(event){
   
     
    // pegar o valor do input
  let inputValue = parseInt(document.querySelector("#input-number").value)

    // botão para a próxima fase
  let button = document.querySelector("#btn-nl")

     // texto caso o valor do input seja vázio.
   let valueNull = document.querySelector(".value-null")
    
    //área de erro caso a resposta esteja errada.

   let errorMessage = document.querySelector(".error-message")
  
   function verifyValueNull(value){
       //função responsavel por ver se o valor do input é vazio
       if(document.querySelector("#input-number").value === ""){
        valueNull.classList.remove("d-none")
        
        errorMessage.classList.add("d-none")
       }
   }
   function verifyQuestion(){

    if(inputValue === 8){
        button.classList.remove("d-none")

        errorMessage.classList.add("d-none")
        valueNull.classList.add("d-none")
    }

    else if(inputValue !== 8){
        errorMessage.classList.remove("d-none")

        button.classList.add("d-none")
        valueNull.classList.add("d-none")
     }
   }
   return[
    verifyQuestion(),
    verifyValueNull()
   ]
}

export default Riddle1