import React from 'react'
import './css/OtherMatters.css'

import OtherMatterHeader from '../otherMatters/components/OtherMattersHeader'
import CoralineCard from './linksCards/CoralineCard'
 import HorrorCard from './linksCards/HorrorCard'
 import AnnabelleCard from './linksCards/AnnabelleCard'
 import JogosBizarrosCard from './linksCards/JogosBizarrosCard'
function OtherMatters(){
    return(
        <div className="articles-links">

            <header className="article-links-header">
                <OtherMatterHeader/>
            </header>
           
        <div className="article-thumbnail-area">
            <CoralineCard/>
            <HorrorCard/>
            <AnnabelleCard/>
            <JogosBizarrosCard/>
        </div>
    </div>  
    )
}

export default OtherMatters