import React from 'react'
import './css/Content.css'

import EdeLorraine from '../images/annabelle/edeLorraine.jpg'
import PeçasMuseuOcultismo from '../images/annabelle/peçasMuseuOcultismo.jpg'
import Bonecas from '../images/annabelle/bonecas.jpg'
import AnnabelleFilme from '../images/annabelle/annabelleFilme.jpg'
import LorraineEAnnabelle from '../images/annabelle/lorraineAnnabelle.jpg'
import Annabelle from '../images/annabelle/annabelle.jpg'

const AVerdadeiraAnnabelle =() => {
        return(
                 <section className="content">

                 <h2>Conheça a verdadeira Annabelle e a sombria história dela.</h2>

                <p>Se você é uma pessoa medrosa é melhor parar por aqui. O texto que você está prestes a ler fala sobre a história real por trás da boneca Annabelle. E, caso você não saiba do que se trata, ela que ficou famosa no cinema por ser possuída pelo demônio.</p>

                
                <p>A história da boneca do mal começou a se popularizar em 2013, depois do filme Invocação do Mal. O longa mostra, basicamente, um dos maiores mistérios explorados pelo casal Ed e Lorraine Warren. Eles foram demonologistas que ganhavam a vida resolvendo casos sobrenaturais, muito famosos na década de 60.</p>

                <p>Mas, antes de voltarmos à história de Annabelle, você precisa entender quem são as pessoas que acabamos de citar.</p>

                <h2>Ed e Lorraine Warren</h2>

                <p>Para quem não sabe, os Warren são pessoas reais que residiam em Monroe, em Connecticut. Por décadas, eles viajaram pelos Estados Unidos resolvendo fenômenos sobrenaturais e possessões demoníacas</p>
                <p>Na verdade, o dom de lidar com os espíritos era de Lorraine. Conta-se que ela tinha visões e sensibilidade para o assunto desde criança, mas que só deixou seu sexto sentido aflorar depois de adulta.</p>

                <figure>
                    <img src={EdeLorraine} alt="Casal Ed e Lorraine Warren, donos do Museu do Ocultismo" className="image"/>
                    <figcaption>
                    Casal Ed e Lorraine Warren, donos do Museu do Ocultismo
                    </figcaption>
                </figure>

                <p>Depois de casada com Ed, eles passaram a resolver casos misteriosos como o de Amityville, que também virou filme. Muitos outros casos envolvendo o casal ficaram famosos e até viraram filme, como o de Annabelle.</p>

                <p>Ao longo da carreira do casal, eles foram armazenando os objetos amaldiçoados dos casos que resolviam. E foram muitos casos. Tantos, que eles acabaram criando o Museu do Ocultismo dos Warren, na casa deles, em Connecticut.</p>
                
                <figure>
                    <img src={PeçasMuseuOcultismo} alt="Peças do Museu do Ocultismo" className="image"/>
                    <figcaption>
                    Peças do Museu do Ocultismo
                    </figcaption>
                </figure>

                <p>Embora Ed tenha falecido em 2006, o local ainda é aberto ao público. De forma geral, o museu conta com uma coleção de respeito, resultante dos 10 mil casos resolvidos ao longo da carreira do casal</p>

                <h2>O espírito de Annabelle</h2>
                <p>A boneca Annabelle de verdade, como não poderia ser diferente, também está no Musou dos Warren. Segundo a história que contaram sobre ela, a boneca era um artigo de colecionador.</p>

                <p>Donna, uma estudante de enfermagem, havia ganhado a boneca de presente da mãe quando se mudou para Connecticut. A garota tinha acabado de começar a faculdade.</p>
                <p>Donna e sua colega de quarto começaram a perceber que quando elas voltavam para casa, a boneca estava sempre em uma posição diferente. Ou pior: muitas vezes, a boneca Annabelle estava em um outro cômodo qualquer.</p>

                <figure>
                    <img src={Bonecas} alt="Comparação entre a boneca original e a do filme" className="image"/>
                    <figcaption>
                    Comparação entre a boneca original e a do filme
                    </figcaption>
                </figure>
                <p>Preocupadas com o que poderia estar acontecendo, as colegas pediram ajuda de um médium. Na época, o homem disse que a boneca estava sendo possuída pelo espírito de uma menina chamada Annabelle.</p>

                <p>Supostamente, a menina havia morrido naquele apartamento há muito tempo e estava feliz pela presença das jovens. Isso foi o suficiente para que elas deixassem o suposto espírito inofensivo viver com elas por meio da boneca</p>


                <h2>Acontecimentos medonhos</h2>

                <p>Mas, não demorou muito tempo para que as coisas na casa começassem a ficar realmente estranhas. Cerca vez, um amigo das garotas, que sempre visitava o apartamento, estava dormindo no sofá e algo ruim realmente aconteceu.</p>

                <p>Ele, que não acreditava em nada sobrenatural e vivia fazendo piada da história, achou que estivesse tendo uma espécie de pesadelo. No entanto, ele estava sendo estrangulado por Annabelle. Quando ele acordou, tirou a boneca de seu pescoço e, recobrando o fôlego, percebeu que seu peito estava todo arranhado.</p>

                    <figure>
                        <img src={AnnabelleFilme}alt="boneca annabelle do filme" className="image"/>
                    </figure>
                <p>Assustadas, as donas do apartamento buscaram a ajuda de um padre chamado Hegan. O padre logo entendeu que alguma coisa muito mais séria estava acontecendo ali e chamou seu superior, Padre Cooke. Esse último era experiente em exorcismos e poderia expulsar o que quer que fosse.</p>

                <h2>Exorcismo de Annabelle</h2>

                <p>Mas, nem mesmo o padre especialista em obras malignas conseguiu lidar com a força de Annabelle sozinho. Foi então que Ed e Lorraine foram chamados.</p>

                <p>Logo de cara, eles viram que não era um espírito humano que estava ali, mas um demônio que manipulava a boneca para tentar possuir uma alma humana, provavelmente a de Donna.</p>

                <p>Padre Cooke, então, fez o exorcismo no apartamento e na boneca e o casal levou Annabelle embora. Eles a guardaram junto com as demais relíquias.</p>

                    <figure>
                        <img src={LorraineEAnnabelle} alt="Lorraine Warren levando Annabelle para o Museu do Ocultismo" className="image"/>
                        <figcaption>
                        Lorraine Warren levando Annabelle para o Museu do Ocultismo
                        </figcaption>
                    </figure>
                <p>No entanto, até chegar lá coisas aconteceram. Durante o caminho de volta, o casal de especialistas percebeu que o caso era muito mais pesado que eles poderiam imaginar.</p>

                <p>Por várias vezes, eles passaram perto de sofrer um acidente sério. Ed, então, resolveu parar o carro, jogar água benta na boneca e fazer nela o sinal da cruz. O ritual acalmou Annabelle, mas de volta à casa do casal, ela ainda manteve suas atividades demoníacas por algum tempo.</p>

                <p>Lorraine contava que, muitas vezes, eles flagraram a boneca levitando ou, simplesmente, em outros lugares da casa. Foi preciso, então, fazer uma espécie de urna de madeira e vidro, onde a boneca permanece trancada até hoje.</p>

                <figure>
                    <img src={Annabelle} alt="annabelle" className="image"/>
                </figure>

                <h2>Melhor não testar…</h2>
                <p>Dizem ainda que um casal de incrédulos visitou o Museu do Ocultismo há alguns anos e pediram para ver Annabelle. O rapaz resolveu fazer graça da história e bateu o dedo no vidro da urna, mandando a boneca provar que tudo aquilo era real.</p>

                <p>O resultado desse desafio foi um acidente de moto que tirou a vida do rapaz. A namorada dele sobreviveu, mas precisou passar um tempo em um hospital psiquiátrico para se livrar do trauma do acidente. Depois ela contou que não se lembrava de nada, apenas de rir sobre a história da boneca e de acordar no hospital.</p>

                <p>Embora a boneca Annabelle original já esteja quieta há muitos anos, ainda assim um padre visita o museu dos Warren duas vezes por semana.</p>

                <p>E então, sentiu aquele arrepiozinho na espinha? Claro que não se sabe o quanto de verdade, de imaginação ou de charlatanismo existe nessa história, mas é no mínimo interessante entender de onde vem toda essa coisa de Annabelle, não acha?</p>




                <p>
                    <a href="https://segredosdomundo.r7.com/annabelle-conheca-verdadeira-historia-da-boneca-do-mal/" target="blank" rel="noreferrer">Fonte das informações</a>
                    
                </p>

                </section>

        )
    }


export default AVerdadeiraAnnabelle