import React from 'react'
import {Link} from 'react-router-dom'
import Coraline from '../../images/Coraline/coralineThumbnail.jpg'

const CoralineCard = () =>{
    return(
        <div className="article-thumbnail">
        <img src={Coraline} className="thumbnail-image" alt="Coraline Thumbnail"/>
        
        <div className="article-thumbnail-texts">
            <h4>Curiosidades sobre Coraline e o Mundo Secreto</h4>
            <p>Descubra nessa matéria algumas curiosidades que talvez você não saiba sobre "Coraline e o Mundo Secreto". As curiosidades vão desde referências até sobre os bastidores do filme</p> 
        </div>
        <div className="view-button-area">
            <Link to="/curiosidades-sobre-coraline" className="view-button">Visualizar</Link> 
            </div>  
    </div>
    )
}

export default CoralineCard