import React from 'react'
import './Welcome.css'
import {Link} from 'react-router-dom'

class Welcome extends React.Component{
    render(){
        return(
                <div className="container-fluid d-flex flex-column justify-content-center">
                    <h1>Olá, escolha uma opção para continuar...</h1>

                    <div className="button-area">
                    <button type="button" className="btn btn-secondary mt-5 p-3 ">
                        <Link to="/riddle1" className="link">Continuar para os enigmas</Link>
                    </button>
                        <button  type="button" className="btn btn-info mt-5 p-3 ">
                            <Link to="/mundo-nerd-home" className="link">Continuar para o site</Link>
                        </button>
                    </div>
                </div>
        )
    }
}

export default Welcome