import React from 'react'
import './css/Navbar.css'
import {Link} from 'react-router-dom'
const Navbar = () =>{
    return(
            <div className="navbar fixed-top">
            <i className="fas fa-ghost logo-desktop text-warning">

            <i className="fa fa-ghost text-danger"></i>
            <i className="fa fa-ghost text-info"></i>
            <i className="fa fa-ghost text-success"></i>

            </i>
            <nav className="navbar navbar-expand-lg navbar-light" id="navbar">
            <i className="fas fa-ghost logo-mobile text-warning">

            <i className="fa fa-ghost text-danger"></i>
            <i className="fa fa-ghost text-info"></i>
            <i className="fa fa-ghost text-success"></i>
            </i>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler"><i className="fas fa-bars"></i>
                <i className="fas fa-times d-none"></i>
                </span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNavDropdown">
                <ul className="navbar-nav">
                <li className="nav-item active">
                    <Link className="nav-link" to="/mundo-nerd-home">
                        <i className="fas fa-home"></i> Home<span className="sr-only">(current)</span></Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to="/mundo-nerd-curiosidades"> <i className="fas fa-question"></i> Curiosidades</Link>
                </li>
                </ul>
            </div>
            </nav>
        </div>
    )
}

export default Navbar