import React,{useEffect} from 'react'
import '../content/css/Content.css'
import Navbar from '../components/Navbar'
import Header from '../components/Header'
import Footer from '../components/Footer'
import  AnnabelleContent from '../content/AnnabelleContent'
import OtherMatters from '../otherMatters/OtherMatters'

const AVerdadeiraAnnabelle = ()=>{

    useEffect(()=>{
        window.scrollTo(0,0)
    },[])//função para sempre renderizar o topo da página.

    useEffect(()=>{
        document.title = "Mundo Nerd"
    },[])
        return(
            <div className="container-fluid">
            <Navbar/>
            <Header/>
            <AnnabelleContent/>
            <OtherMatters/>
           <Footer/>
           </div>
        )
    }


export default AVerdadeiraAnnabelle