import React,{useEffect} from 'react'
import '../content/css/Content.css'
import Navbar from '../components/Navbar'
import Header from '../components/Header'
import Footer from '../components/Footer'
import FilmesAmaldiçoadosContent from '../content/F-AmaldiçoadosContent'
import OtherMatters from '../otherMatters/OtherMatters'




const FilmesAmaldiçoados = () =>{

    useEffect(()=>{
        window.scrollTo(0,0)
    },[])//função para sempre renderizar o topo da página.

    useEffect(()=>{
        document.title = "Mundo Nerd"
    },[])
        return(
            <div>
            <Navbar/>
            <Header/>
            <FilmesAmaldiçoadosContent/>
            <OtherMatters/>
           <Footer/> 
           </div>
        )
        
    }



export default FilmesAmaldiçoados