import React from 'react'
import './css/Content.css'


import OExorcista from '../images/filmesAmaldiçoados/01-O-Exorcista-1024x576.jpg'
import BebeRosemary from '../images/filmesAmaldiçoados/02-O-Bebê-de-Rosemary-1024x550.jpg'
import OCorvo from '../images/filmesAmaldiçoados/03-O-Corvo-1024x640.jpg'
import AProfecia from '../images/filmesAmaldiçoados/A-profecia-1024x683.jpg'
import Poltergeist from '../images/filmesAmaldiçoados/05-Poltergeist-1024x682.jpg'
import Sobrenatural from '../images/filmesAmaldiçoados/06-Sobrenatural-1024x427.jpg'
import OExorcismo from '../images/filmesAmaldiçoados/07-O-Exorcismo-de-Emily-Rose-1024x672.jpg'
import HorrorEmAmityville from '../images/filmesAmaldiçoados/08-Horror-em-Amityville-1024x576.jpg'
import Possesão from '../images/filmesAmaldiçoados/09-Possessão-1024x681.jpg'
import OChamado from '../images/filmesAmaldiçoados/10-O-Chamado-2-1024x669.jpg'


const FilmesAmaldiçoadosContent = () =>{
        return(
                <section className="content">

                 <h2>Os Filmes Amaldiçoados <span className="text-red">na Vida Real</span></h2>

                <p>Os filmes de terror já possuem uma atmosfera medonha por si só, porém nessa matéria iremos ver filmes que o terror não ocorreu apenas na ficção e sim na vida real,membros da equipe mortos repentinamente, sets inundados são só alguns dos acontecimentos...</p>
                 <h3>1. O Exorcista (1973)</h3>

                <img src={OExorcista} alt="O Exorcista 1973" className="image"/>
                 <p>arece que o demônios assombraram muito mais do que a personagem Regan (Linda Blair) em O Exorcista. A maior parte do filme se passa no quarto da garota, onde os rituais de exorcismo são conduzidos. Aliás, quando você pensa em O Exorcista é muito provável que venha à mente uma cena que se passa ali.</p>

                 <p>Quando estava sendo filmado, em 1972, <b>todo o set da produção foi arrasado por um incêndio</b>, com uma exceção: o quarto de Regan, que permaneceu intacto. Apesar de terem chamado o padre jesuíta Thomas M. King para abençoar o set, parece que isso não foi suficiente. Dizem que pelo menos <b>nove pessoas</b> envolvidas com o longa, incluindo os atores Jack MacGowran e Vasiliki Maliaros, <b>morreram até que a produção de terror fosse concluída.</b></p>

                 <h3>2. O Bebê de Rosemary(1968)</h3>

                <img src={BebeRosemary} alt="O Bebê de Rosemary (1968)" className="image"/>
                 <p>Há algo de muito errado com O Bebê de Rosemary e nem estamos falando da gestação da personagem. A maldição aqui parece ter acompanhado pessoas envolvidas com a produção. Aliás, o ator Sidney Blackmer, que interpretou o líder do coven, Roman Castevet, já avisava que “nada de bom poderia vir deste monte de ‘ave Satanás’”. </p>

                 <p>Após as filmagens, o produtor William Castle teve um grave problema com pedras nos rins e alucinava que Rosemary estava atrás dele no hospital com uma faca. Outro episódio mais trágico foi o do compositor Krzysztof Komeda, que caiu em uma festa e teve danos cerebrais que resultaram em um coma e, consequentemente, com sua morte.</p>

                 <p>O caso mais famoso envolve o diretor Roman Polanski. Pouco depois do lançamento do filme, a atriz Sharon Tate, que era namorada do diretor, foi brutalmente assassinada pelos seguidores de Charles Manson. Ela estava grávida de oito meses de um filho de Polanski.</p>

                 <h3>3. O Corvo (1994)</h3>
                 <img src={OCorvo} alt="O Corvo (1994)" className="image"/>
                 <p>Neste filme, Eric (Brandon Lee) é um músico que ressuscita após seu violento assassinato na véspera de seu casamento. Ele se torna uma espécie de vingador sobrenatural gótico que quer acertar as contas com seus assassinos.</p>

                 <p>Este foi o <b>último filme de Brandon Lee</b>, que foi morto ainda durante as filmagens. Graças a uma confusão e negligência da equipe, o ator foi atingido em uma cena por uma bala de verdade, em vez de balas de festim – causando sua morte. Antes deste acidente fatal, outros membros da equipe já tinham se machucado. O mais assustador é que antes destes acidentes Brandon já falava que <b>sua família era amaldiçoada</b> e que ele morreria logo – ele é filho do ator Bruce Lee, que também morreu em circunstâncias misteriosas antes de concluir as filmagens de Jogo da Morte.</p>

                 <h3>4. A Profecia (1976)</h3>
                 <img src={AProfecia} alt="4. A Profecia (1976)" className="image"/>

                 <p>Diz a profecia que o filho do anticristo seria criado em uma família poderosa para que ele pudesse iniciar seus planos diabólicos na Terra. Outra profecia menos famosa diz que <b>filmes que lidam com temas envolvendo o diabo estão amaldiçoados</b>. A produção de A Profecia não é exceção à regra.</p>

                 <p>Antes mesmo das filmagens terem oficialmente iniciado, o elenco do filme de Richard Donner já parecia condenado. Quando estava a caminho de Londres, justamente para trabalhar no longa, o ator Gregory Peck estava em <b>um avião que foi atingido por um raio</b>. Uma experiência parecida ocorreu com o produtor Mace Neufeld, quando seu avião de Los Angeles foi eletrocutado poucas semanas depois.</p>

                 <p>Outras tragédias associadas ao filme incluem uma bomba plantada por terroristas no hotel em que Neufeld e sua esposa estavam hospedados em Londres. Um evento mais trágico foi a <b>morte do adestrador de animais do longa:</b> apenas um dia depois de ele ter comparecido ao set para lidar com os babuínos na famosa cena do zoológico o treinador foi atacado por um tigre</p>

                 <p>Mas o incidente mais macabro e chocante de A Profecia ocorreu dois meses após a estreia do filme, em 6/6/76. O profissional de efeitos especiais John Richardson estava dirigindo na Holanda com sua assistente Liz Moore quando eles se envolveram em um acidente. Richardson sobreviveu, mas Moore foi decapitada. Uma placa que estava nos arredores do acidente adiciona um elemento de terror ao acontecimento: nela estava escrito “<b>Ommen, 66,6km</b>” – “omen” significa profecia em inglês e é o título original do filme</p>

                <h3>5. Poltergeist: O Fenômeno (1982)</h3>
                <img src={Poltergeist} alt="5. Poltergeist: O Fenômeno (1982)" className="image"/>

                <p><b>Poltergeist</b> é um filme sobre uma família que, sem saber, se muda para uma casa construída em cima de um antigo cemitério indígena. Os espíritos daqueles enterrados ali não gostam nada disso e <b>assombram a família</b> com uma série de eventos sobrenaturais. Sabendo disso, você logo imagina que o diretor Tobe Hooper e o roteirista e produtor Steven Spielberg não mexeriam com os mortos, não é mesmo? Pois foi bem isso o que eles fizeram ao utilizar <b>esqueletos humanos de verdade</b> no cenário em que Diane é arrastada para uma piscina em obras.</p>

                <p>Os mais supersticiosos apontam isso como o principal causador das tragédias que aconteceram com membros do elenco. A jovem <b>Dominique Dunne foi assassinada</b> em 1982, apenas alguns dias antes de completar 23 anos e no mesmo ano do lançamento do filme. Já <b>Heather O’Rourke</b>, que interpretou a pequena Carol Anne, <b>faleceu seis anos</b> depois de uma estenose intestinal mal diagnosticada – ela tinha apenas 12 anos. <b>Dois atores que trabalharam nas sequências dos filmes também morreram </b>repentinamente de doenças ou em decorrência de cirurgias.</p>

                <h3>6. Sobrenatural (2010)</h3>
                <img src={Sobrenatural}alt="6. Sobrenatural (2010)" className="image"/>

                <p>Inspirado nas investigações reais do casal Ed e Lorraine Warren, <b>Sobrenatural </b>é o primeiro de uma série de filmes que lidam com paranormalidade. Mas as maldições não acontecem apenas na telona.</p>

                <p>Muitas das pessoas que trabalharam nesta produção de terror relataram que sentiam um forte desconforto e como se houvesse uma <b>energia negativa muito forte no set</b> enquanto filmavam. Além disso, estas pessoas relataram que podiam ouvir uma espécie de <b>zumbido vindo de várias salas vazias</b> pelo set, mesmo quando ninguém estava por lá.</p>

                <h3>7. O Exorcismo de Emily Rose (2005)</h3>
                <img src={OExorcismo} alt="O Exorcismo de Emily Rose (2005)" className="image"/>
                
                <p>Inspirado em uma história real de 1976,<b> O Exorcismo de Emily Rose</b> conta a história de uma jovem que lida com uma possessão demoníaca, interpretada por Jennifer Carpenter. A adolescente alemã que inspirou a história morreu durante um exorcismo em 1976.</p>

                <p>Como se a história real não fosse assustadora o suficiente, o filme supostamente também tinha um <b>set amaldiçoado</b>. A atriz Jennifer Carpenter relatou que durante as filmagens viveu algumas experiências paranormais bem estranhas. Além disso, ela disse que nesta época todas as noites <b>o rádio dela ligava sozinho</b>, sem qualquer explicação. Fica a dúvida se isso ocorria às 3h, o horário que, segundo o filme, é “<b>a hora do diabo</b>”.</p>

                <h3>8. Horror em Amityville (1979 e 2005)</h3>
                <img src={HorrorEmAmityville} alt="Horror em Amityville" className="image"/>

                <p>Esta história vagamente inspirada em um acontecimento real em uma casa localizada em Nova York já recebeu inúmeras adaptações, sequências e spin-offs nas telas. A primeira versão foi lançada em 1979.</p>

                <p>Como se a história de terror da família Lutz não fosse suficientemente assustadora, há alguns relatos do set que são bem sombrios. James Brolin, o ator que interpretou George Lutz na versão de 1979, alega que quando ele estava considerando aceitar este papel <b>uma roupa caiu sozinha de seu closet</b>. Décadas depois, Ryan Reynolds, que interpretou o mesmo personagem no remake de 2005, disse que durante as filmagens <b>ele acordava todas as noites às 3h</b> sem motivo aparente. Se juntarmos isso ao plot de O Exorcismo de Emily Rose sabemos que esta é a tal “hora do diabo”.</p>

                <h3>9. Possessão (2012)</h3>
                <img src={Possesão} alt="Possessão (2012)" className="image"/>

                <p>Dizem que este filme era tão amaldiçoado que não apenas apavorou a equipe de produção e o elenco, como aconteceu em outros filmes desta lista. Segundo o ator Jeffrey Dean Morgan, os eventos estranhos que ocorreram durante as filmagens o converteram de cético a alguém que <b> acredita seriamente que a paranormalidade existe.</b></p>

                <p>Elenco e equipe de produção relatam ter presenciado <b> estranhas correntes de ar frio</b> no set, mas isso é o de menos. Um dos episódios mais estranhos envolve <b>a caixa dibbuk</b> usada no filme. Pausa para quem não sabe: diz o folclore judeu que um dibbuk é <b>um espírito maligno condenado a vagar pelo mundo</b> pelos seus pecados – a tal da caixa estaria aprisionando este espírito. Voltando à produção de Possessão, após as filmagens terem terminado, a tal da caixa foi colocada em um depósito. Pouco depois, ela foi <b>destruída em um incêndio</b> no tal depósito. Será que isso libertou algum espírito maligno?</p>

                <h3>10. O Chamado 2 (2005)</h3>
                <img src={OChamado} alt="O Chamado 2" className="image"/>

                <p>Se você também tem medo de atender o telefone para uma voz assustadora que diga “sete dias” você já está bem familiarizado com a história do <b>vídeo maligno</b> e <b>da garotinha do poço</b> que irão te matar em uma semana. Em <b>O Chamado 2</b>, continuação da versão de Hollywood deste clássico japonês, Rachel (Naomi Watts) e seu filho Aiden (David Dorfman) se esforçam para fazer com que o espírito de Samara (Kelly Stables) finalmente descanse.</p>

                <p>No primeiro filme ficamos sabendo que Samara morreu porque sua mãe lhe jogou em um poço, onde ela se afogou. Aliás, <b> água e inundações</b> são temas bem recorrentes quando o assunto é O Chamado. E é justamente isso o que torna tão assustador o fato de que <b>o set foi inundado durante as filmagens</b> da sequência. Seria a Samara querendo se libertar do poço?</p>

                <p>
                    <a href="https://darkside.blog.br/10-filmes-terror-amaldicoados-vida-real/#" target="blank" rel="noreferrer">Fonte das informações</a>
                    
                </p>
                </section>

        )
    }


export default  FilmesAmaldiçoadosContent