import React from 'react'

export default function CuriosidadesContent() {
    return (
        <section className="content">
            <h2>Curiosidades sobre o Mundo Nerd</h2>
            <ol className="ol">
                <li>
                    O site <b>Mundo Nerd</b> foi criado em uma tarde, aonde eu queria programar um site mas não sabia o tema, vendo sobre enigmas e curiosidades a idéia do site surgiu na minha mente.
                </li>
                <li>
                    Para criação do site foi usado  <b>React JS</b> uma biblioteca que eu gosto muito e quero melhorar meus conhecimentos, <b>Bootstrap</b> e os icones do <b>Font Awesome</b>.
                </li>
                <li>
                    Até chegar na versão final, o site passou por <b>pelo menos 20 alterações</b>.
                </li>
                <li>
                    A criação de todo o site durou <b>pelo menos 20 horas.</b>
                </li>
                <li>
                    A primeira versão do site iria falar apenas <b> sobre terror</b>, porém depois de algumas mudanças o tema do site mudou para abordar curiosidades do mundo geek em um geral, mas o terror ainda continua vivo nas matérias do site.
                </li>
                <li>
                    Foi muito divertido fazer esse site, pude ampliar meus conhecimentos e me divertir, foram horas muito bem aproveitadas.
                </li>
                <li>
                    Foi com esse site que pude usar muito do vanilla js,validações e verificações são feitas em vanilla js e um pouco de React Hooks.
                </li>
                <li>
                   O site foi finalizado em 27/11/2020.
                </li>
            </ol>
            <p>Obrigado por visitar o site, espero que tenha gostado do conteúdo e do site, fico feliz de ter você aqui, te vejo por ai visitante <i className="fas fa-heart"></i>.</p>
        </section>
    )
}
