import React from 'react'
import './css/Content.css'


import Coraline2 from '../images/Coraline/coraline2.png'
import Coralisa from '../images/Coraline/Coralisa.jpg'
import MansaoRosa from '../images/Coraline/mansaoRosa.gif'
import Coraline from '../images/Coraline/coraline4.jpg'


const CuriosidadesSobreCoraline =() => {
        return(
                 <section className="content">

                 <h2>Curiosidades sobre Coraline e o Mundo Secreto <i className="fas fa-cat" id="fa-cat"></i></h2>

                <p>Apesar de ser um desenho animado, engana-se quem pensa que o filme Coraline e o Mundo Secreto é só uma “história para crianças”. Lançado em fevereiro de 2009, o filme pode ser bem assustador - mas, ao mesmo tempo, conquistar o público de várias idades.</p>

                <img src={Coraline2}  className="image" alt="Coraline"/>
                <p>Ao se mudar para uma nova casa, Coraline encontra uma porta secreta que a leva para um mundo diferente, onde tudo parece ser muito mais perfeito do que a realidade. Seus novos pais nesse universo são mais atenciosos e amorosos, e a garota tem tudo o que precisa. No entanto, o que parece ser um conto de fadas, acaba virando uma situação bem perigosa e macabra.</p>

                <p>Se você é fã do filme ou ficou curioso para saber mais sobre ele, está no lugar certo! Confira 16 curiosidades incríveis sobre a história:</p>

                <ol className="ol">
                    <li>O filme foi baseado no livro do autor Neil Gaiman, mesmo autor da famosa história “Sandman”.</li>

                    <li>A obra é considerada a primeira animação em stop-motion para o formato 3D. Inclusive, até hoje, foi o desenho mais longo já produzido utilizando essa técnica!</li>

                    <li>O filme teve uma indicação para melhor animação no Oscar de 2010.</li>

                    <li>“Os Simpsons” fizeram uma paródia de Coraline na 9º temporada, quando Lisa se torna “Coralisa”.
                        <img src={Coralisa} className="image" alt="Coralisa"/>
                    </li>

                    <li>A animação levou cerca de quatro anos para ficar pronta. Só o trabalho de fotografia levou 18 meses!</li>

                    <li>Mensagens subliminares em todos os lugares: a gema de ovo que a Outra Mãe despeja em uma vasilha forma a imagem do personagem principal do filme “O Estranho Mundo de Jack” do ano 1993, também dirigido por Henry Selick. </li>

                    <li>A loja de departamento Linden é uma referência a Harry Linden, produtor do filme. Além disso, a empresa de mudanças Ranft Bros, tem seu nome inspirado nos artistas de animação Jerome e Joe Ranft!</li>

                    <li>No filme, o dinheiro usado para pagar os funcionários da Ranft Bros estampa o rosto do diretor, Henry Selick. Já os chinelos usados pelo Outro Pai são uma referência ao filme “No Limite da Imaginação”, também dirigido por Selick</li>

                    <li>A adaptação cinematográfica é bem fiel ao livro, fazendo apenas algumas mudanças: na obra literária, por exemplo, a cor da mansão da família não é mencionada. Por isso, os detalhes da “mansão cor-de-rosa” são exclusivos do filme.
                        
                        <img src={MansaoRosa} className="image" alt="Pink Palace"/>
                    </li>

                    <li>No filme, Coraline faz amizade com o neto da dona da mansão, Wyborne, mas esse personagem não existe no livro. Ele foi criado para apresentar o local a Coraline, enquanto no livro, passamos um longo tempo acompanhando as explorações e descobertas da garota sozinha.

                        <img src={Coraline} className="image" alt="Coraline 2"/>
                    </li>
                </ol>

                <p>Essas foram as curisodades sobre Coraline espero que tenha gostado,confie no gato e  cuidado com uma versão de seus pais com botões nos olhos, as aparências enganam...</p>

                <p>
                    <a href="https://rollingstone.uol.com.br/noticia/coraline-e-o-mundo-secreto-13-curiosidades-que-voce-nao-sabia-sobre-historia/" target="blank" rel="noreferrer">Fonte das informações</a>
                    
                </p>

                </section>

        )
    }


export default CuriosidadesSobreCoraline